import { notFound } from 'next/navigation';
import Link from 'next/link';
import { mockStories, mockComments } from '@/interfaces/mockData';
import CommentSection from '@/app/components/CommentSection';

interface StoryPageProps {
    params: Promise<{ id: string }>;
}

export default async function StoryPage({ params }: StoryPageProps) {
    const { id } = await params;
    const story = mockStories.find(s => s.id === id);

    if (!story) {
        notFound();
    }

    const comments = mockComments[id] || [];

    const formatDate = (date: Date) => {
        return date.toLocaleDateString('tr-TR', {
            year: 'numeric',
            month: 'long',
            day: 'numeric'
        });
    };

    const formatNumber = (num: number) => {
        if (num >= 1000) {
            return `${(num / 1000).toFixed(1)}k`;
        }
        return num.toString();
    };

    return (
        <div className="story-page">
            <div className="story-container">
                <Link href="/" className="back-button">
                    <span>â†</span>
                    Ana Sayfaya DÃ¶n
                </Link>

                <article className="story-article">
                    <header className="story-header">
                        <div className="story-meta">
                            <span className="category-badge">{story.category}</span>
                            <span className="story-date">{formatDate(story.createdAt)}</span>
                        </div>

                        <h1 className="story-title">{story.title}</h1>

                        <div className="author-section">
                            <div className="author-info">
                                <span className="author-avatar">{story.author.avatar}</span>
                                <span className="author-name">{story.author.name}</span>
                            </div>

                            <div className="story-stats">
                                <span className="stat">
                                    <span className="stat-icon">ğŸ‘ï¸</span>
                                    {formatNumber(story.stats.views)} gÃ¶rÃ¼ntÃ¼lenme
                                </span>
                                <span className="stat">
                                    <span className="stat-icon">ğŸ’¬</span>
                                    {formatNumber(story.stats.comments)} yorum
                                </span>
                                <span className="stat">
                                    <span className="stat-icon">â¤ï¸</span>
                                    {formatNumber(story.stats.likes)} beÄŸeni
                                </span>
                            </div>
                        </div>
                    </header>

                    <div className="story-content">
                        {story.content.split('\n\n').map((paragraph, index) => (
                            <p key={index}>{paragraph}</p>
                        ))}
                    </div>
                </article>

                <CommentSection storyId={id} initialComments={comments} />
            </div>
        </div>
    );
}
